<h4 id="按钮组" tabindex="-1"><a class="header-anchor" href="#按钮组" aria-hidden="true">#</a> 按钮组</h4>
<div class="component-box">
  <div class="component-box-top"><ClientOnly>
    <wd-button-group size="small" type="success">
      <wd-button
        round-shape="none"
        icon="home-outlined"
        @click="clickHandler"
        >成功按钮</wd-button
      >
      <wd-button
        size="large"
        round-shape="none"
        icon="home-outlined"
        type="primary"
        @click="clickHandler"
        >主要按钮</wd-button
      >
      <wd-button
        round-shape="none"
        icon="home-outlined"
        type="primary"
        @click="clickHandler"
        >按钮</wd-button
      >
      <wd-button round-shape="circle" type="primary"
        >C</wd-button
      >
    </wd-button-group>
    </ClientOnly>
  </div>
  <div class="component-box-bottom">
    <div class="component-title">ButtonGroup</div>
    <div class="component-desc">
      在按钮组中的按钮会自动加上间隔，并且可以设置按钮组中的按钮的大小和类型，如果按钮也设置了大小和类型，则以按钮组上的属性会被覆盖。
    </div>
  </div>
</div>
